<template>
	<div class="card-delete-success">
		<tip-page :tipTitle="tipTitle" :tipMsg="tipMsg" tipButton="" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			tipTitle: '',
			tipMsg: ''
		};
	},
	created() {
		if(this.$route.params.type === 'add'){
			this.tipTitle = '';
			this.tipMsg = '';
		} else{
			this.tipTitle = '';
			this.tipMsg = '';
		}
	},
	methods: {
		back() {
			this.$router.push({name: 'MyBankCard'});
		}
	}
};

</script>


