<template>
	<div class="my-bank-card-credit-detail">
		<div class="field-part">
			<mt-cell title="" :value="detail.PAYER_ACCOUNT"></mt-cell>
		</div>
		<div class="field-part">
			<div class="head-hint"></div>
			<mt-field v-model="detail.CVN2" label="CVN2" placeholder="3" :attr="{ maxlength: 3 }"></mt-field>
			<mt-field v-model="detail.CARDVAL" label="" placeholder="/40421" :attr="{ maxlength: 4 }"></mt-field>
			<mt-field v-model="detail.ACCT_ALIAS" label="" placeholder="()" :attr="{ maxlength: 12 }"></mt-field>
			<cell-password :options=" cellPassword "></cell-password>
		</div>
		<div class="tip-hint"></div>
		<div class="button-bottom">
			<mt-button @click="showDialogSmsVerify" type="primary" size="large"></mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			detail: {
				PAYER_ACCOUNT: '',
				ACCT_ALIAS: '',
				CVN2: '',
				CARDVAL: ''
			},
			cellPassword: {
                title: '',
                rule: 'number'
            },
            dialogSmsVerify: {
                title: '',
                btnConfirmText: '',
                btnGetText: ''
            }
		};
	},
	methods: {
		showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.addCredit();
            }, (data) => {
            });
        },
        addCredit() {
        	service.addCredit({
        		ACCT_TYPE: '3',
        		PAYER_ACCOUNT: this.detail.PAYER_ACCOUNT,
        		ACCT_NAME: '',
        		ACCT_ALIAS: this.detail.ACCT_ALIAS,
        		CVN2: this.detail.CVN2,
        		CARDVAL: this.detail.CARDVAL,
        		OPT_TYPE: '3',
        		PAY_PWD: '',
        		MSGCODE: '',
        		INDEXCODE: ''
        	})
        	.then((data) => {
        		this.$router.push({name: 'MyBankCardCreditSuccess'});
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.detail.PAYER_ACCOUNT = this.$route.params.cardNum;
	}
};

</script>


