<template>
    <div>
        <tip-page :tipTitle="title" :tipMsg="msg"></tip-page>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary"></mt-button>
        </div>
    </div>
    
</template>

<script>


export default {
    data() {
        return {
            title: '',
            msg: 'pos'
        };
    },
    created(){
        var type = this.$route.query.type || '';
        if( type == 'close'){
            this.title = '';
            this.msg = 'pos';
        }
    },
    mounted(){
    },
    methods: {
        back (){
            this.$router.push('/');
        }
    }
};
</script>

<style lang="scss">
.field-action {
    margin-top: 66px;
}
</style>



