<template>
	<div class="my-bank-card-credit-add">
		<div class="head-hint hint-reset"></div>
		<div class="field-part">
			<mt-field v-model="cardNum" label="" placeholder=""></mt-field>
		</div>
		<div class="tip-hint"></div>
		<div class="button-bottom">
			<mt-button @click="next" type="primary" size="large" :disabled="!cardNum"></mt-button>
		</div>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			cardNum: ''
		};
	},
	methods: {
		next() {
			this.$router.push({name: 'MyBankCardCreditDetail', params: {cardNum: this.cardNum}});
		},
		noneActive() {
			this.$hybrid.call({
                method: 'confirm',
                params: {
                    title: '',
                    message: '',
                    confirmButtonText: ''
                },
                callback: (params) => {
                	if(params.action == 1){
						this.$router.push({name: 'OneActiveDetail', params: {card: this.cardNum, fromAdd: true}});
					}
                }

            });
		},
		noneTransactionPwd() {
			this.$hybrid.call({
                method: 'confirm',
                params: {
                    title: '',
                    message: '',
                    confirmButtonText: ''
                },
                callback: (params) => {
                	if(params.action == 1){
						this.$router.push({name: 'CreditManagePwdTransactionApplySet', params: {card: this.cardNum, fromAdd: true}});
					}
                }

            });
		}
	}
};

</script>

<style lang="scss">
	
.my-bank-card-credit-add{
	.hint-reset{
		margin-top: 0;
		padding-top: 20px;
	}
}

</style>


