</template>

<script>

// import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            dialogSmsVerify: {
                title: '',
                btnConfirmText: '',
                btnGetText: ''
            }
        };
    },
    created(){
    },
    mounted(){
    },
    methods: {
        toClick ( type ) {
            if( type == 'device'){
                this.$confirm('', {
                    yes: { text: '' },
                    no: { text: '' }
                }).then((result)=>{
                    this.showDialogSmsVerify();
                }, (result)=>{
                    console.log(result);
                });
            }
        },
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('...');
            }, (data) => {
            });
        }
    }
};
</script>

